import Vue from 'vue'
import Router from 'vue-router'
import Home from './components/Home.vue'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/performance',
      name: 'performance',
      // route level code-splitting
      // this generates a separate chunk (performance.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: () => import(/* webpackChunkName: "performance" */ './components/Performance.vue')
    },
    {
      path: '/project/:id',
      name: 'project',
      component: () => import(/* webpackChunkName: "project" */ './components/Project.vue')
    },
    {
      path: '/contact',
      name: 'contact',
      component: () => import(/* webpackChunkName: "contact" */ './components/Contact.vue')
    },
    {
      path: '/faces',
      name: 'faces',
      component: () => import(/* webpackChunkName: "faces" */ './components/Faces.vue')
    },
    {
      path: '/journal',
      name: 'journal',
      component: () => import(/* webpackChunkName: "journal" */ './components/Journal.vue')
    },
    {
      path: '/article/:id',
      name: 'article',
      component: () => import(/* webpackChunkName: "singlearticle" */ './components/Article.vue')
    },
    {
      path: '*',
      redirect: '/'
    }
  ]
})
