import api from 'api-client'

/**
 * @api {post} /uploadpdf Upload
 * @apiDescription Upload new presentation file
 *
 * @apiName postPdf
 * @apiGroup Presentation
 * @apiVersion 1.0.0
 *
 * @apiParam {File} pdf File
 */
async function uploadPdf (file) {
  return api({
    url: `/uploadpdf`,
    method: 'POST',
    data: file,
    headers: { 'Content-Type': 'multipart/form-data' }
  })
}

/**
 * @api {post} /send_mail Send
 * @apiDescription Send feedback email
 *
 * @apiName sendFeedback
 * @apiGroup Feedback
 * @apiVersion 1.0.0
 *
 * @apiParam {String} name Sender name
 * @apiParam {String} phone Sender phone
 * @apiParam {String} email Sender email
 * @apiParam {String} message Sender message
 *
 * @apiParamExample {json} Request:
 *   {
 *     "name": "TestName",
 *     "phone": "777",
 *     "email": "someemail@gmail.com",
 *     "message": "Test msg lol"
 *   }
 */
async function sendFeedback ({ name, phone, email, message }) {
  return api({
    url: `/send_mail`,
    method: 'POST',
    data: {
      name,
      phone,
      email,
      message
    }
  })
}

/**
 * @api {get} /get_projects Get list
 * @apiDescription Get brief list of projects
 *
 * @apiName getProjects
 * @apiGroup Project
 * @apiVersion 1.0.0
 *
 * @apiSuccess {String} id Project ID
 * @apiSuccess {String} title Project title
 * @apiSuccess {String} previewPhotoPath Project preview URL
 * @apiSuccess {String} releaseYear Project release datetime
 *
 * @apiSuccessExample {json} 2xx
 *     {
 *       "2014": [
 *         {
 *           "id": "25ce009d-3ebd-4e9f-b884-7e3fbfcee25d",
 *           "title": "project8",
 *           "previewPhotoPath": "/Files/25ce009d-3ebd-4e9f-b884-7e3fbfcee25d/gori-1920x1080-reka-16728.jpeg",
 *           "releaseYear": "2014-12-10T00:00:00"
 *         }
 *       ]
 *     }
 */
async function getProjects () {
  return api({
    url: `/get_projects`,
    method: 'GET'
  })
}

/**
 * @api {post} /get_project_details Get
 * @apiDescription Get project by ID
 *
 * @apiName getProject
 * @apiGroup Project
 * @apiVersion 1.0.0
 *
 * @apiParam {String} id Project ID
 *
 * @apiSuccess {Object} project
 * @apiSuccess {String} project.id Project ID
 * @apiSuccess {String} project.title Project title
 * @apiSuccess {String} project.description Project description
 * @apiSuccess {String} project.projectLink
 * @apiSuccess {String} project.videoLink
 * @apiSuccess {String} project.behanceLink
 * @apiSuccess {String} project.bannerPhotoPath
 * @apiSuccess {String} project.previewPhotoPath
 * @apiSuccess {String} project.releaseYear
 * @apiSuccess {String} project.createdOn
 * @apiSuccess {Boolean} project.isDisabled
 * @apiSuccess {String} project.sOrder
 * @apiSuccess {String} nextProjectId
 * @apiSuccess {String} previousProjectId
 *
 * @apiSuccessExample {json} 2xx
 *   {
 *     "project": {
 *       "id": "25ce009d-3ebd-4e9f-b884-7e3fbfcee25d",
 *       "title": "project8",
 *       "description": "zxcvb",
 *       "projectLink": "http://fiddle.digital/presentation/fiddle-02/index.html",
 *       "videoLink": null,
 *       "behanceLink": "https://www.behance.net",
 *       "bannerPhotoPath": "/Files/25ce009d-3ebd-4e9f-b884-7e3fbfcee25d/gori-1920x1080-reka-16728.jpeg",
 *       "previewPhotoPath": "/Files/25ce009d-3ebd-4e9f-b884-7e3fbfcee25d/gori-1920x1080-reka-16728.jpeg",
 *       "releaseYear": "2014-12-10T00:00:00",
 *       "createdOn": "2019-03-09T22:31:16.947",
 *       "isDisabled": false,
 *       "sOrder": null
 *     },
 *     "nextProjectId": "218f251b-06f9-4aa0-85d3-d37485f848e2",
 *     "previousProjectId": null
 *   }
 */
async function getProject (id) {
  return api({
    url: `/get_project_details?id=${id}`,
    method: 'POST'
  })
}

