/**
 * @api {post} /get_article Get
 * @apiDescription Get article by ID
 *
 * @apiName getArticle
 * @apiGroup Article
 * @apiVersion 1.0.0
 *
 * @apiParam {String} id Article ID
 *
 * @apiSuccess {String} id Article ID
 * @apiSuccess {String} title Article title
 * @apiSuccess {String} body Article HTML
 * @apiSuccess {String} createdOn
 * @apiSuccess {Boolean} isDisabled
 * @apiSuccess {Number} sOrder
 *
 * @apiSuccessExample {json} 2xx
 *   {
 *     "id": "c146ed06-0e1c-4948-b01a-5a57187c1e02",
 *     "title": "Обзор смартфона Samsung Galaxy J6+",
 *     "body": "<div>HTML</div>",
 *     "createdOn": "2019-03-02T17:15:40.687",
 *     "isDisabled": false,
 *     "sOrder": 0
 *   }
 */
async function getArticle (id) {
  return api({
    url: `/get_article?id=${id}`,
    method: 'POST'
  })
}

export default {
  uploadPdf,
  sendFeedback,
  getProjects,
  getProject,
  getLastProject,
  getArticles,
  getArticle
}
