<template>
  <transition name="component-fade" mode="out-in">
    <component :is="currentView"/>
  </transition>
</template>

<script>
  import BaseView from '@/views/BaseView'

  export default {
    name: 'App',
    components: {
      'base-view': BaseView
    },
    data () {
      return {
        currentView: 'base-view'
      }
    }
  }
</script>

<style lang="scss">
  @import "sass/main";
</style>
