<script>
  import { DateTime } from 'luxon'
  import { mapActions, mapGetters } from 'vuex'

  export default {
    name: 'Home',
    computed: {
      created () {
        if (this.lastProject.createdOn) {
          return DateTime
            .fromISO(this.lastProject.createdOn)
            .toLocaleString({ year: 'numeric' })
        }
        return ''
      },

      ...mapGetters({
        lastProject: 'last'
      })
    },

    methods: {
      ...mapActions({
        load: 'last'
      })
    },

    beforeRouteEnter (to, from, next) {
      next(vm => {
        vm.load()
      })
    }
  }
</script>

