import Vue from 'vue'
import Vuex from 'vuex'
import API from '@/api'

Vue.use(Vuex)

export default new Vuex.Store({
  strict: process.env.NODE_ENV !== 'production' && process.env.NODE_ENV !== 'stage',
  state: {
    loading: false,
    projects: {},
    project: {
      nextProjectId: null,
      previousProjectId: null,
      project: {
        id: null,
        title: '',
        description: '',
        projectLink: '',
        videoLink: null,
        behanceLink: '',
        bannerPhotoPath: '',
        previewPhotoPath: '',
        releaseYear: '',
        createdOn: '',
        isDisabled: false,
        sOrder: null
      }
    },
    last: {},
    articles: {},
    article: {}
  },

  mutations: {
    loading (state, loading) {
      state.loading = loading
    },
    projects (state, projects) {
      state.projects = projects
    },
    project (state, project) {
      state.project = project
    },
    last (state, last) {
      state.last = last
    },
    articles (state, articles) {
      state.articles = articles
    },
    article (state, article) {
      state.article = article
    }
  },

  getters: {
    loading (state) {
      return state.loading
    },
    projects (state) {
      return state.projects
    },
    project (state) {
      return state.project
    },
    last (state) {
      return state.last
    },
    articles (state) {
      return state.articles
    },
    article (state) {
      return state.article
    }
  },

  actions: {
    projects ({ commit }) {
      commit('loading', true)
      API.getProjects().then(response => {
        commit('projects', response)
        commit('loading', false)
      })
    },
    project ({ commit }, id) {
      commit('loading', true)
      API.getProject(id).then(response => {
        commit('project', response)
        commit('loading', false)
      })
    },
    last ({ commit }) {
      commit('loading', true)
      API.getLastProject().then(response => {
        commit('last', response)
        commit('loading', false)
      })
    },
    articles ({ commit }) {
      commit('loading', true)
      API.getArticles().then(response => {
        commit('articles', response)
        commit('loading', false)
      })
    },
    article ({ commit }, id) {
      commit('loading', true)
      API.getArticle(id).then(response => {
        commit('article', response)
        commit('loading', false)
      })
    }
  }
})
